:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  color: #212121;
  width: auto;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  display: flex;
}

.onstrat {
  margin-top: 20%;
}

.update {
  min-height: 2em;
  z-index: 1;
  width: 100%;
  background-color: rgba(0, 0, 0, .43);
  justify-content: center;
  padding-bottom: 1%;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

input {
  text-align: center;
  width: 330px;
  margin-top: 10px;
  font-size: x-large;
}

.gallery {
  width: 1200px;
  height: auto;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  grid-auto-rows: 240px;
  margin-top: -10%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  display: grid;
  position: relative;
}

.photo-card {
  margin-top: 12%;
  display: block;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.stats {
  height: 50px;
  width: 100%;
  background-color: rgba(35, 110, 175, .443);
  border-style: groove;
  border-color: rgba(114, 112, 112, 0);
  flex-direction: row;
  align-items: center;
  padding-left: 40px;
  display: inline-flex;
}

.stats-item {
  margin-left: 10px;
}

.photo-card img:hover, .stats-item :hover {
  cursor: pointer;
  color: #2368a8;
  transform: scale(1.03);
}

.photo-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.load-more {
  width: 200px;
  height: 40px;
  margin-top: 50px;
}

.hide {
  display: none;
}

.lightbox {
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--animation-duration) var(--timing-function);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.lightbox.is-open {
  opacity: 1;
  pointer-events: initial;
}

.lightbox__overlay {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .8);
  position: absolute;
  top: 0;
  left: 0;
}

.lightbox__content {
  transition: transform var(--animation-duration) var(--timing-function);
  transform: scale(.95);
}

.lightbox.is-open .lightbox__content {
  transform: scale(1);
}

.lightbox__image {
  width: auto;
  height: auto;
  max-height: 100vh;
  max-width: 100vw;
  display: block;
}

.lightbox__button {
  width: 48px;
  height: 48px;
  color: #fff;
  cursor: pointer;
  transition: background-color var(--animation-duration) var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60%;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.lightbox__button:hover, .lightbox__button:focus {
  background-color: rgba(255, 255, 255, .5);
}

